import { Component, OnInit } from '@angular/core';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite/ngx';
import { Platform } from '@ionic/angular';

@Component({
  selector: 'app-cadastro',
  templateUrl: './cadastro.page.html',
  styleUrls: ['./cadastro.page.scss'],
})
export class CadastroPage {
  
  databaseObj: SQLiteObject;
  name_model: string = "";
  sobrenome_model: string = "";
  row_data: any = [];
  readonly database_name: string = "database_app.db";
  readonly table_name: string = "userstable";

    constructor(
      private platform: Platform,
      private sqlite: SQLite
    ) {
      this.platform.ready().then(() => {
        this.createDB();
      }).catch(error => {
        console.log(error);
      })
    }

      createDB() {
        this.sqlite.create({
          name: this.database_name,
          location: 'default'
        })
          .then((db: SQLiteObject) => {
            this.databaseObj = db;
            alert('database_app Database Created!');
          })
          .catch(e => {
            alert("error " + JSON.stringify(e))
          });
      }

      createTable() {
        this.databaseObj.executeSql('CREATE TABLE IF NOT EXISTS ' + this.table_name + ' (pid INTEGER PRIMARY KEY, Name varchar(50), Sobrenome varchar(100))', [])
          .then(() => {
            alert('Table Created!');
          })
          .catch(e => {
            alert("error " + JSON.stringify(e))
          });
      }

      

      insertRow() {
        if (!this.name_model.length) {
          alert("Enter Name ");
          return;
        }
        if (!this.sobrenome_model.length) {
          alert("Enter  Sobrenome");
          return;
        }
        this.databaseObj.executeSql('INSERT INTO ' + this.table_name + ' (Name) VALUES ' + this.name_model  + ' (Sobrenome) VALUES ' + this.sobrenome_model , [])
          .then(() => {
            alert('Rows Inserted!');
            this.getRows();
          })
          .catch(e => {
            alert("error " + JSON.stringify(e))
          });
      }

      getRows() {
        this.databaseObj.executeSql("SELECT * FROM " + this.table_name, [])
          .then((res) => {
            this.row_data = [];
            if (res.rows.length > 0) {
              for (var i = 0; i < res.rows.length; i++) {
                this.row_data.push(res.rows.item(i));
              }
            }
          })
          .catch(e => {
            alert("error " + JSON.stringify(e))
          });
      }

      deleteRow(item) {
        this.databaseObj.executeSql("DELETE FROM " + this.table_name + " WHERE pid = " + item.pid, [])
          .then((res) => {
            alert("Row Deleted!");
            this.getRows();
          })
          .catch(e => {
            alert("error " + JSON.stringify(e))
          });
      }
     
  }